import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    Avatar,
    Button,
    IconButton,
    Input,
    PanelHeader,
    PanelHeaderBack,
    ScreenSpinner,
    Snackbar,
    WriteBarIcon
} from "@vkontakte/vkui";
import {
    Icon16Chevron, Icon16Recent,
    Icon24Like,
    Icon28ChevronRightOutline,
    Icon28ReportOutline,
    Icon28ShareOutline, Icon28WriteOutline
} from "@vkontakte/icons";
import bridge from "@vkontakte/vk-bridge";
import {
    decOfNum,
    getBase64Image,
    getSrcUrl,
    getUrlParams,
    isPlatformDesktop, isPlatformIOS,
    openUrl,
    shortIntegers
} from "../../js/utils";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";

const isDesktop = isPlatformDesktop();

export class Tags extends PureComponent {

    render() {
        const
            {t} = this.props,
            {_tags} = t
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                >
                    {
                        isDesktop && 'Метки'
                    }
                </PanelHeader>
                <div className='Tags'>
                    {
                        !isDesktop && <h2 style={{textAlign: 'left'}}>Метки</h2>
                    }
                    <div className='TagsList'>
                        {
                            _tags().map(value =>
                                <div>
                                    {value[1]}
                                    <div>
                                        <h1>{value[2]}</h1>
                                        <h3>{value[3]}</h3>
                                    </div>
                                </div>
                            )
                        }
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

Tags.defaultProps = {};

Tags.propTypes = {
    t: PropTypes.object
};

export default Tags;