import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    Avatar,
    Button,
    IconButton,
    Input,
    PanelHeader,
    PanelHeaderBack,
    ScreenSpinner,
    Snackbar,
    WriteBarIcon
} from "@vkontakte/vkui";
import {
    Icon16Chevron, Icon16Recent,
    Icon24Like,
    Icon28ChevronRightOutline,
    Icon28ReportOutline,
    Icon28ShareOutline, Icon28WriteOutline
} from "@vkontakte/icons";
import bridge from "@vkontakte/vk-bridge";
import {
    decOfNum,
    getBase64Image,
    getSrcUrl,
    getUrlParams,
    isPlatformDesktop, isPlatformIOS,
    openUrl,
    shortIntegers
} from "../../js/utils";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";

const isDesktop = isPlatformDesktop();

export class PictureInfo extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            canGetMore: true
        };

        this.shareStory1 = this.shareStory1.bind(this);
        this.shareStory2 = this.shareStory2.bind(this);

        const
            {t, param, current} = this.props,
            {socket, state} = t,
            list = state[param]
        ;
        socket.call('pictures.isLiked', {game_id: list[current].id}, r => {
            this.setState({isLiked: r.response});
        });
    }

    componentDidMount() {

    }

    shareStory1(gameId, pic) {
        this.props.t.setPopout(<ScreenSpinner/>);
        try {
            const
                {createCanvas, loadImage} = require('canvas')
            ;

            loadImage(getSrcUrl(require(`../../assets/drawing/stories/Story3.png`))).then(async background => {
                const
                    canvas = createCanvas(background.width, background.height),
                    ctx = canvas.getContext('2d'),
                    canvasData = await getBase64Image(pic)
                ;
                ctx.drawImage(background, 0, 0);
                ctx.drawImage(await loadImage(canvasData), 198, 618, 683, 683);

                try {
                    await bridge.send('VKWebAppShowStoryBox', {
                        background_type: 'image',
                        blob: canvas.toDataURL('image/png'),
                        attachment: {
                            url: `https://vk.com/app${getUrlParams().vk_app_id}#picture${gameId}`,
                            text: 'go_to',
                            type: 'url'
                        }
                    });
                    this.props.t.socket.call('games.share', {gameId});
                } catch (e) {
                }
                this.props.t.setPopout(null);
            });
        } catch (e) {
            console.error(e);
            this.props.t.setPopout(null);
            this.props.t.setState({
                snackbar: <Snackbar onClose={() => this.props.t.setState({snackbar: null})}>
                    Произошла ошибка
                </Snackbar>
            });
        }
    }

    shareStory2(word, gameId, pic) {
        try {
            this.props.t.setPopout(<ScreenSpinner/>);
            const
                {createCanvas, loadImage} = require('canvas')
            ;

            loadImage(getSrcUrl(require(`../../assets/drawing/stories/Story1.png`))).then(async background => {
                try {
                    const
                        canvas = createCanvas(background.width, background.height),
                        ctx = canvas.getContext('2d'),
                        canvasData = await getBase64Image(pic),
                        title = word.substring(0, 1).toUpperCase() + word.substring(1)
                    ;
                    ctx.drawImage(background, 0, 0);

                    ctx.font = '64px TT Commons Demibold';
                    ctx.textAlign = 'center';
                    ctx.fillStyle = '#616161';
                    ctx.fillText(title, 539, 708 + 13 + 489);

                    ctx.drawImage(await loadImage(canvasData), 198, 618, 683, 683);

                    await bridge.send('VKWebAppShowStoryBox', {
                        background_type: 'image',
                        blob: canvas.toDataURL('image/png'),
                        attachment: {
                            url: `https://vk.com/app${getUrlParams().vk_app_id}#picture${gameId}`,
                            text: 'go_to',
                            type: 'url'
                        }
                    });
                    this.props.t.socket.call('games.share', {gameId});
                    this.props.t.setPopout(null);
                } catch (e) {
                    console.error(e);
                    this.props.t.setPopout(null);
                    this.props.t.setState({
                        snackbar: <Snackbar onClose={() => this.props.t.setState({snackbar: null})}>
                            Произошла ошибка
                        </Snackbar>
                    });
                }
            });
        } catch (e) {
            console.error(e);
            this.props.t.setPopout(null);
            this.props.t.setState({
                snackbar: <Snackbar onClose={() => this.props.t.setState({snackbar: null})}>
                    Произошла ошибка
                </Snackbar>
            });
        }
    }

    render() {
        const
            {t, current, method, param, user_id} = this.props,
            {n_canvasData} = t.state,
            list = t.state[param],
            game = list[current],
            {isLiked, canGetMore} = this.state,
            needButtonResumeDraw = game.drawerId === t.state.vk_user.id,
            ButtonResumeDraw = needButtonResumeDraw && (<Button
                style={{
                    marginTop: isDesktop ? 10 : 16
                }}
                className='ButtonResumeDraw'
                before={<Icon28WriteOutline width={16} height={16}/>}
                size='m' mode='gradient' stretched
                onClick={async () => {
                    const {admin, vk_donut} = t.state.user;
                    if (admin || vk_donut) {
                        await t.setState({
                            isOnlineDrawing: false,
                            word: {nom: game['Word.nom']},
                            pictureUrl_: game['Picture.url'],
                            gameId_: game.id
                        });
                        t.go('game');
                    } else {
                        if (isPlatformIOS()) {
                            t.go('vk_donut_ios');
                        } else {
                            t.go('vk_donut');
                        }
                    }
                }}
            >
                Дорисовать
            </Button>)
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                />
                <div className='PictureInfo'>
                    <div className='Canvas_Container'>
                        <h2>{game['Word.nom'].substring(0, 1).toUpperCase() + game['Word.nom'].substring(1)}</h2>
                        <img alt='canvas' src={n_canvasData || game['Picture.url']}/>
                        <div className='Actions_Container'>
                            <div className='PictureTime'>
                                {Math.round((game.endTime - game.startTime) / 1000)} с.
                            </div>
                            <div
                                style={isLiked ? {color: '#F9F9F9', background: '#F16384'} : {}} className='LikeButton'
                                onClick={() => {
                                    t.setPopout(<ScreenSpinner/>);
                                    t.socket.call('pictures.like', {game_id: game.id}, r => {
                                        if (r.response) {
                                            list[current] = {...game, ['Picture.likes']: r.response.likes};
                                            t.setState({like_picturesList: list});
                                            this.setState({isLiked: r.response.isLiked});
                                        }
                                        t.setPopout(null);
                                    });
                                }}>
                                {shortIntegers(game['Picture.likes'])} <Icon24Like style={{marginLeft: 4}}/>
                            </div>
                            <div className='ReportButton' onClick={() => {
                                t.setPopout(<ScreenSpinner/>);
                                t.socket.call('games.report', {game_id: game.id}, r => {
                                    if (r.response) {
                                        t.setState({
                                            snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                                Жалоба отправлена
                                            </Snackbar>
                                        });
                                    } else {
                                        t.setState({
                                            snackbar: <Snackbar onClose={() => t.setState({snackbar: null})}>
                                                Жалоба уже отправлена
                                            </Snackbar>
                                        });
                                    }
                                    t.setPopout(null);
                                });
                            }}>
                                <Icon28ReportOutline width={16} height={16}/>
                            </div>
                        </div>
                    </div>
                    <div className='Author_Container' onClick={() => {
                        //openUrl(`https://vk.com/id${game.drawerId}`);
                        t.setPopout(<ScreenSpinner/>);
                        t.socket.call('users.getById', {id: game.drawerId}, async r => {
                            await t.setState({
                                author: r.response
                            });
                            t.socket.call('games.getByDrawerId', {id: game.drawerId}, async r => {
                                await t.setState({
                                    author_works: r.response
                                });
                                t.go('author');
                                t.setPopout(null);
                            });
                        });
                    }}>
                        <div>Автор</div>
                        <div>
                            <Avatar shadow={false} size={36} src={vk_local_users[game.drawerId].photo_100}/>
                            <div>{vk_local_users[game.drawerId].first_name} {vk_local_users[game.drawerId].last_name}</div>
                        </div>
                    </div>
                    <div className='Actions_Container'>
                        <Button
                            before={<Icon28ShareOutline width={16} height={16}/>}
                            size='m' mode={needButtonResumeDraw ? 'secondary' : 'gradient'}
                            onClick={() => {
                                if (game.drawerId !== t.state.vk_user.id) {
                                    this.shareStory1(game.id, game['Picture.url']);
                                } else {
                                    this.shareStory2(game['Word.nom'], game.id, game['Picture.url']);
                                }
                            }}
                        >
                            Поделиться
                        </Button>
                        <Button
                            style={{marginTop: isDesktop && 10}}
                            disabled={!canGetMore}
                            after={<Icon16Chevron/>}
                            size='m' mode='secondary'
                            onClick={() => {
                                if (current === (list.length - 1)) {
                                    if (method && method.length > 0) {
                                        t.setPopout(<ScreenSpinner/>);
                                        t.socket.call(method, {
                                            offset: t.state[param].length,
                                            limit: 10,
                                            id: user_id
                                        }, async r => {
                                            if (r.response.length > 0) {
                                                await getVKUsers(r.response.map(value => value.drawerId));
                                                await t.setState({[param]: [...t.state[param], ...r.response]});
                                                t.socket.call('pictures.isLiked', {game_id: r.response[0].id}, r => {
                                                    this.setState({isLiked: r.response});
                                                });
                                                t.setState({
                                                    like_currentPictureSelected: current + 1,
                                                    n_canvasData: undefined
                                                });
                                            }
                                            this.setState({canGetMore: r.response.length > 0});
                                            t.setPopout(null);
                                        });
                                    } else {
                                        this.setState({canGetMore: false});
                                    }
                                } else {
                                    t.socket.call('pictures.isLiked', {game_id: list[current + 1].id}, r => {
                                        this.setState({isLiked: r.response});
                                    });
                                    t.setState({like_currentPictureSelected: current + 1, n_canvasData: undefined});
                                }
                            }}
                        >
                            Дальше
                        </Button>
                        {
                            (needButtonResumeDraw && isDesktop) && ButtonResumeDraw
                        }
                    </div>
                    {
                        (needButtonResumeDraw && !isDesktop) && ButtonResumeDraw
                    }
                </div>
            </React.Fragment>
        )
    }
}

PictureInfo.defaultProps = {};

PictureInfo.propTypes = {
    t: PropTypes.object,
    current: PropTypes.number,
    method: PropTypes.string,
    param: PropTypes.string,
    user_id: PropTypes.number
};

export default PictureInfo;