import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import '../../css/Drawing/Global.css';

import {get, getRandomInts, getUrlParams, isPlatformDesktop, loadFonts, openUrl} from "../../js/utils";
import {vk_local_users, getVKUsers} from "../../js/drawerapp/utils";
import {
    Button,
    ModalCard,
    ModalRoot,
    Panel, ScreenSpinner, Snackbar,
    View
} from '@vkontakte/vkui';
import {
    Icon20AchievementCircleFillBlue,
    Icon20FireCircleFillRed,
    Icon20RecentCircleFillYellow,
    Icon28ChevronRightOutline, Icon56CakeCircleFillPurple, Icon56ServicesOutline
} from "@vkontakte/icons";

import fetch from 'node-fetch';

import {registerFont, createCanvas, loadImage} from 'canvas';
import request from "request";

import {ReactComponent as IconOnboard} from "../../assets/drawing/icons/Icon.svg";
import {ReactComponent as IconError} from "../../assets/drawing/icons/IconError.svg";

import Main from "./Main";
import Gallery from "./Gallery";
import Rating from "./Rating";
import Search from "./Search";
import Game from "./Game";
import Result from "./Result";
import Placeholder from "./Placeholder";

import Socket from '../../js/socket';
import eruda from 'eruda';
import Likes from "./Likes";
import PictureInfo from "./PictureInfo";
import Author from "./Author";
import Tags from "./Tags";
import Friends from "./Friends";
import FriendLobby from "./FriendLoby";

import (`../../css/Drawing/${window.location.search.length > 0 && JSON.parse('{"' + decodeURI(window.location.search.substring(1)).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"') + '"}').vk_platform === 'desktop_web' ? 'Desktop' : 'Mobile'}.css`);

const
    socket = new Socket()
;
let
    bannersInterval,
    connectInterval
;

export const
    MODAL_CARD_PLAY_MODE = 'play-mode',
    MODAL_CARD_NEW_UPDATE = 'new-update'
;

class Drawing extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activePanel: 'main',
            history: ['main'],

            activeModal: null,
            modalHistory: [],

            colors: [
                '#000000',
                '#FFFFFF',
                '#E63902',
                '#EA5A03',
                '#FA8A00',
                '#FBB101',
                '#FDE401',
                '#85B201',
                '#019317',
                '#019C8B',
                '#035AA2',
                '#00247A',
                '#500A6D',
                '#A9236B'
            ],

            online: 0,

            drawerId: 0,
            lobbyId: 0,

            isOnlineDrawing: true,

            bannersCount: 3,
            banners: [],
            defaultBannerData: {
                title: 'Заголовок',
                domain: 'vk.com',
                trackingLink: 'https://vk.com',
                ctaText: 'Перейти',
                advertisingLabel: 'Реклама',
                iconLink: 'https://sun9-7.userapi.com/c846420/v846420985/1526c3/ISX7VF8NjZk.jpg',
                description: 'Описание рекламы',
                ageRestrictions: "14+",
                statistics: [
                    {url: '', type: 'playbackStarted'},
                    {url: '', type: 'click'}
                ]
            },
            connected: false,
            dataUpdated: false,

            author_works: []
        };

        this.componentDidMount = this.componentDidMount.bind(this);
        this.updateData = this.updateData.bind(this);
        this.back = () => {
            const {history, popout, activeModal} = this.state;

            if (popout !== null && popout !== undefined)
                return;

            if (activeModal !== null) {
                this.setState({activeModal: null});
                window.history.pushState({pop: 'popout'}, 'Title');
                return;
            }

            if (history.length > 1) {
                if (history[history.length - 1] === 'search') {
                    socket.call('games.search');
                    this.setState({
                        snackbar: <Snackbar onClose={() => this.setState({snackbar: null})}>
                            Поиск игры отменён
                        </Snackbar>
                    });
                }

                if (history[history.length - 1] === 'friend_lobby') {
                    socket.call('friends.leaveLobby', {owner_id: this.state.friend_lobby.owner_id});
                }

                history.pop();
                this.setState({activePanel: history[history.length - 1], history});
                if (history[history.length - 1] !== 'search')
                    this.setState({snackbar: null});
            } else {
                bridge.send('VKWebAppClose', {status: 'success'});
            }
        };
        this.go = (panel) => {
            const {history, activeModal} = this.state;
            if (activeModal !== null) {
                this.setState({activeModal: null, modalHistory: []});
            }
            if (history[history.length - 1] !== panel) {
                history.push(panel);
                window.history.pushState({activePanel: panel}, 'Title');
                this.setState({activePanel: panel, history, snackbar: null});
            }
        };
        this.setActivePanel = (panel) => {
            this.setState({activePanel: panel, history: [panel], snackbar: null});
        };
        this.setActiveModal = (activeModal = null) => {
            let {modalHistory} = this.state;

            if (activeModal === null) {
                modalHistory = [];
            } else if (modalHistory.indexOf(activeModal) !== -1) {
                modalHistory = modalHistory.splice(
                    0,
                    modalHistory.indexOf(activeModal) + 1
                );
            } else {
                modalHistory.push(activeModal);
            }

            this.setState({
                activeModal,
                modalHistory,
            });
        };
        this.setPopout = (popout) => {
            this.setState({popout});
        };
        this.socket = socket;

        window.onload = () => {
            const
                windowHeight = document.documentElement.clientHeight,
                scale = 100 / 812 * windowHeight / 100
            ;
            document.body.style.setProperty('--scale-value', scale);
        };
    }

    async componentDidMount() {
        loadFonts();

        window.addEventListener('popstate', e => {
            e.preventDefault();
            this.back();
        });

        bridge.subscribe(async ({detail: {type, data}}) => {
            if (type !== undefined) console.log(type, data);
            if (type === 'VKWebAppUpdateConfig') {
                const schemeAttribute = document.createAttribute('scheme');
                schemeAttribute.value = data.scheme ? data.scheme === 'client_light' ? 'bright_light' : data.scheme : 'bright_light';
                document.body.attributes.setNamedItem(schemeAttribute);
            } else if (type === 'VKWebAppViewRestore') {
                this.setState({popout: null});
            }
        });

        const
            isShowOnboard = (await bridge.send('VKWebAppStorageGet', {keys: ['onboard']})).keys[0].value === '',
            isShowVkGroup = (await bridge.send('VKWebAppStorageGet', {keys: ['vk_group1']})).keys[0].value === '',
            isShowUpdate = (await bridge.send('VKWebAppStorageGet', {keys: ['update_friends_mode']})).keys[0].value === '',
            vk_user = await bridge.send('VKWebAppGetUserInfo'),
            vk_user_ = getVKUsers([vk_user.id])
        ;
        await this.setState({isShowOnboard, isShowVkGroup, vk_user});

        bridge.send('VKWebAppEnableSwipeBack');

        this.connect();

        socket.subscribe('exit', r => {
            this.setState({placeholderText: r.title, banned: r.banned});
            this.setPopout(null);
            if (r.banned) {
                this.setState({placeholderNeed: 2});
                this.setActivePanel('banned');
            } else {
                this.setState({placeholderNeed: 1});
                this.setActivePanel('placeholder');
            }
            bridge.send('VKWebAppInit');
        });
        socket.onConnectionError((e) => {
            console.error(e);
            this.setState({placeholderText: 'Не удалось подключиться к серверу.', placeholderNeed: 1});
            this.setPopout(null);
            this.setActivePanel('placeholder');
            bridge.send('VKWebAppInit');
        });
        socket.onDisconnect(() => {
            this.setPopout(null);
            if (this.state.placeholderNeed !== 2) {
                this.setState({placeholderText: ''});
                this.setActivePanel('placeholder');
            }
            bridge.send('VKWebAppInit');
        });

        await new Promise(resolve =>
            socket.onConnect(async () => {
                await this.setState({connected: true, placeholderNeed: 0});
                console.log('Connected');
                this.setActivePanel('main');
                await this.updateData();

                if (isShowVkGroup && !isShowOnboard) {
                    setTimeout(() => {
                        this.go('group_vk');
                    }, 1000);
                } else if (isShowUpdate && !isShowOnboard) {
                    setTimeout(() => {
                        this.setActiveModal(MODAL_CARD_NEW_UPDATE);
                    }, 1000);
                } else if (!isShowOnboard) {
                    this.onChangeHash();
                }
                resolve(true);
            })
        );

        clearInterval(bannersInterval);
        bannersInterval =
            setInterval(() => {
                const {banners, bannersCount} = this.state;
                for (let i = 0; i < bannersCount; i++) {
                    bridge.send('VKWebAppGetAds', {}).then(data => {
                        if (!(banners[i] && banners[i] === false))
                            banners[i] = data;
                    });
                }
                this.setState({banners});
            }, 3000);

        bridge.send('VKWebAppInit');
    }

    connect() {
        socket.connect('https://draw.avocado.special.vk-apps.com', getUrlParams());
        setTimeout(() => {
            if (!this.state.dataUpdated) {
                this.updateData();
            }
            if (!this.state.connected) {
                clearInterval(connectInterval);
                connectInterval = setInterval(() => {
                    if (!this.state.connected) {
                        socket.connect('https://draw.avocado.special.vk-apps.com', getUrlParams());
                    } else {
                        clearInterval(connectInterval);
                    }
                }, 1000);
            }
        }, 1000);
    }

    async updateData() {
        await new Promise(resolve => {
            socket.call('users.getTopNumber', {}, r => {
                this.setState({top_number: r.response});
            });
            socket.call('users.getTop', {}, async r => {
                const
                    user_ids = r.response.map(value => value.id)
                ;
                await getVKUsers(user_ids);

                this.setState({top: r.response.map(value => ({...vk_local_users[value.id], ...value}))});
            });
            socket.call('users.get', {}, r => {
                this.setState({user: r.response});

                socket.call('games.getByDrawerId', {id: this.state.vk_user.id, limit: 10}, r => {
                    this.setState({gamesAsDrawer: r.response});
                    let user_ids = r.response.map(value => value.drawerId);

                    socket.call('games.getByWinnerId', {id: this.state.vk_user.id, limit: 10}, r => {
                        user_ids = [...user_ids, ...r.response.map(value => value.drawerId)];

                        getVKUsers(user_ids);
                        setTimeout(async () => {
                            await this.setState({gamesAsWinner: r.response, dataUpdated: true});
                            resolve(true);
                        }, 400);
                    });
                });
            })
        });

        this.setState({time: Date.now()});
        return true;
    }

    onChangeHash() {
        if (window.location.hash.startsWith('#')) {
            const
                hash = window.location.hash.substring(1),
                keys = [
                    {
                        key: 'author',
                        do: value => {
                            this.setPopout(<ScreenSpinner/>);
                            this.socket.call('users.getById', {id: value}, async r => {
                                if (r.response !== null) {
                                    await getVKUsers([value]);
                                    await this.setState({
                                        author: r.response
                                    });
                                    this.socket.call('games.getByDrawerId', {id: value}, async r => {
                                        await this.setState({
                                            author_works: r.response
                                        });
                                        setTimeout(() => {
                                            this.go('author');
                                            this.setPopout(null);
                                        }, 400);
                                    });
                                } else {
                                    this.setPopout(null);
                                }
                            });
                        }
                    },
                    {
                        key: 'picture',
                        do: async value => {
                            try {
                                const
                                    gameId = parseInt(value)
                                ;

                                this.setPopout(<ScreenSpinner/>);
                                this.socket.call('games.getById', {id: gameId}, async r => {
                                    if (r.response !== null) {
                                        const {drawerId} = r.response;
                                        await getVKUsers([drawerId]);
                                        await this.setState({
                                            gamesAsAuthor: [r.response],
                                            like_currentPictureSelected: 0,
                                            like_method: '',
                                            like_param: 'gamesAsAuthor',
                                            like_user_id: drawerId
                                        });
                                        setTimeout(() => {
                                            this.go('picture_info');
                                            this.setPopout(null);
                                        }, 400);
                                    } else {
                                        this.setPopout(null);
                                    }
                                });
                            } catch (e) {
                                console.error(e);
                            }
                        }
                    }
                ]
            ;

            for (const key of keys) {
                if (hash.startsWith(key.key)) {
                    key.do(hash.substring(key.key.length));
                }
            }
        }
    }

    _tags() {
        return [
            [
                'tag_interesting',
                <Icon20FireCircleFillRed/>,
                'Интересный автор',
                'Метка выдаётся при достижении 1000 лайков'
            ],
            [
                'tag_rapidfire',
                <Icon20RecentCircleFillYellow/>,
                'Скорострел',
                'Метка выдаётся за картину, нарисованную и угаданную за 5 секунд'
            ],
            [
                'tag_creative',
                <Icon20AchievementCircleFillBlue/>,
                'Креативный',
                'Метка выдаётся вручную администрацией, если та посчитает твои картины креативными'
            ]
        ];
    }

    _tag_modal(tag_name) {
        const tag_info = this._tags().find(value => value[0] === tag_name);
        return (
            <ModalCard
                key={tag_name}
                id={tag_name}
                onClose={() => this.setActiveModal(null)}
                icon={React.cloneElement(tag_info[1], {width: 56, height: 56})}
                header={tag_info[2]}
                subheader={tag_info[3]}
                actions={
                    <Button
                        size='l'
                        mode='gradient_blue'
                        onClick={() => this.go('tags')}
                    >
                        Подробнее
                    </Button>
                }
            ></ModalCard>
        );
    }

    render() {
        const
            {
                activePanel, activeModal,
                popout, snackbar,

                isShowOnboard, isShowVkGroup,
                vk_user, user, banned,

                placeholderText,

                gamesAsWinner, gamesAsDrawer,

                drawerId, winnerId, lobbyId, gameId, word,
                like_picturesList, like_currentPictureSelected, like_method, like_param, like_user_id,

                isOnlineDrawing,

                friend_lobby
            } = this.state,
            showConnecting = !(vk_user && user && gamesAsWinner && gamesAsDrawer),

            modal = (
                <ModalRoot activeModal={activeModal}>
                    {
                        this._tags().map(value => this._tag_modal(value[0]))
                    }
                    <ModalCard
                        id={MODAL_CARD_PLAY_MODE}
                        onClose={() => this.setActiveModal(null)}
                        icon={<Icon56ServicesOutline/>}
                        header='Какую игру запускать?'
                        actionsLayout='horizontal'
                        actions={
                            [
                                <Button
                                    size='l'
                                    mode='gradient_blue'
                                    onClick={() => this.go('search')}
                                >
                                    Обычная
                                </Button>,
                                <Button
                                    size='l'
                                    mode='gradient_violet'
                                    onClick={() => this.go('friends')}
                                >
                                    С друзьями
                                </Button>
                            ]
                        }
                    ></ModalCard>
                    <ModalCard
                        id={MODAL_CARD_NEW_UPDATE}
                        onClose={() => this.setActiveModal(null)}
                        icon={<Icon56CakeCircleFillPurple/>}
                        header='Обновление'
                        subheader='Хочешь узнать, что нового появилось в игре?'
                        actions={
                            <Button
                                size='l'
                                mode='gradient_violet'
                                onClick={() => {
                                    bridge.send('VKWebAppStorageSet', {key: 'update_friends_mode', value: '1'});
                                    this.setActiveModal(null);
                                    openUrl('https://vk.com/wall-208964042_481');
                                }}
                            >
                                Читать
                            </Button>
                        }
                    ></ModalCard>
                </ModalRoot>
            )
        ;

        return (
            <View
                activePanel={(activePanel !== 'placeholder' && activePanel !== 'banned') ? (showConnecting ? 'connecting' : isShowOnboard ? 'onboard' : activePanel) : activePanel}
                popout={popout}
                modal={modal}
            >
                {
                    [
                        {
                            id: 'onboard',
                            component: <Placeholder
                                icon={<IconOnboard/>}
                                title='Добро пожаловать'
                                description='Здесь Вы можете почувствовать себя в роли как художника, так и Шерлока. Рисуйте картины и угадывайте чужие.'
                                buttonIcon={<Icon28ChevronRightOutline width={16} height={16}/>}
                                buttonText='Продолжить'
                                buttonOnClick={() => {
                                    bridge.send('VKWebAppStorageSet', {key: 'onboard', value: '1'});
                                    this.setState({isShowOnboard: false});
                                }}
                            />
                        },
                        {
                            id: 'connecting',
                            component: <Placeholder
                                icon={<IconOnboard/>}
                                title='Подключение'
                                description='Выполняется подключение к серверу...'
                            />
                        },
                        {
                            id: 'placeholder',
                            component: <Placeholder
                                icon={<IconError/>}
                                title='Соединение потеряно'
                                description={placeholderText}
                                buttonText='Переподключиться'
                                buttonOnClick={() => {
                                    this.connect();
                                }}
                            />
                        },
                        {
                            id: 'main',
                            component: <React.Fragment>
                                <Main t={this} time={this.state.time}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'gallery',
                            component: <React.Fragment>
                                <Gallery t={this}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'rating',
                            component: <React.Fragment>
                                <Rating t={this}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'likes',
                            component: <React.Fragment>
                                <Likes t={this}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'picture_info',
                            component: <React.Fragment>
                                <PictureInfo
                                    t={this}
                                    current={like_currentPictureSelected}
                                    method={like_method} param={like_param} user_id={like_user_id}
                                />
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'author',
                            component: <Author t={this}/>
                        },
                        {
                            id: 'search',
                            component: <Search t={this}/>
                        },
                        {
                            id: 'game',
                            component: <React.Fragment>
                                <Game t={this} drawerId={drawerId} lobbyId={lobbyId} online={isOnlineDrawing}
                                      word={word}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'result',
                            component: <React.Fragment>
                                <Result t={this} drawerId={drawerId} winnerId={winnerId} word={word} gameId={gameId}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'tags',
                            component: <Tags t={this}/>
                        },
                        {
                            id: 'friends',
                            component: <React.Fragment>
                                <Friends t={this}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'friend_lobby',
                            component: <React.Fragment>
                                <FriendLobby t={this} friend_lobby={friend_lobby}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'group_vk',
                            component: <Placeholder
                                icon={<img src={require('../../assets/drawing/icons/Icon_Group.png')} alt='icon'/>}
                                title='С возвращением!'
                                description='Не хочешь вступить в наше сообщество ВКонтакте? Именно там публикуются все крутые обновления'
                                buttonText='Вступить'
                                buttonOnClick={async () => {
                                    bridge.send('VKWebAppStorageSet', {key: 'vk_group1', value: '1'});
                                    this.setState({isShowVkGroup: false});
                                    try {
                                        await bridge.send('VKWebAppJoinGroup', {group_id: 208964042});
                                    } catch (e) {
                                    }
                                    this.back();
                                }}
                                buttonMode='gradient_gray'
                                buttonBack={true}
                                t={this}
                            />
                        },
                        {
                            id: 'vk_donut',
                            component: <Placeholder
                                icon={<img src={require('../../assets/drawing/icons/Icon_Donut.png')} alt='icon'/>}
                                title='VK Donut'
                                description='Чтобы получить доступ к дополнительным функциям, оформите подписку.'
                                buttonText='Перейти'
                                buttonOnClick={() => {
                                    openUrl('https://vk.com/donut/draw_app');
                                }}
                                buttonMode='gradient_donut'
                                buttonBack={true}
                                t={this}
                            />
                        },
                        {
                            id: 'vk_donut_ios',
                            component: <Placeholder
                                icon={<img src={require('../../assets/drawing/icons/Icon_Donut.png')} alt='icon'/>}
                                title='VK Donut'
                                description='Для уточнения информации, напишите нам в группу ВКонтакте.'
                                buttonText='Написать'
                                buttonOnClick={() => {
                                    openUrl('https://vk.com/draw_app');
                                }}
                                buttonMode='gradient_donut'
                                buttonBack={true}
                                t={this}
                            />
                        },
                        {
                            id: 'banned',
                            component: <Placeholder
                                icon={<img src={require('../../assets/drawing/icons/Icon_Group.png')} alt='icon'/>}
                                title='Блокировка'
                                description={`Вы были заблокированы за нарушение правил. Дата разблокировки: ${new Date(banned + (24 * 60 * 60 * 1000)).toLocaleDateString('ru', {
                                    day: 'numeric',
                                    month: 'numeric',
                                    year: 'numeric',
                                    hour: 'numeric',
                                    minute: 'numeric'
                                })}`}
                                buttonText='Перейти в группу'
                                buttonOnClick={() => {
                                    openUrl('https://vk.com/club' + 208964042);
                                }}
                                buttonMode='gradient_gray'
                                t={this}
                            />
                        },
                    ].map(({id, component}, index) =>
                        <Panel id={id} key={`Panel__${index}`}>
                            {component}
                        </Panel>
                    )
                }
            </View>
        );
    }
}

export default Drawing;