import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {PanelHeader, PanelHeaderBack, PromoBanner, ScreenSpinner, Snackbar} from "@vkontakte/vkui";
import {getBase64Image, getSrcUrl, getUrlParams, isPlatformDesktop} from "../../js/utils";
import bridge from "@vkontakte/vk-bridge";
import {getVKUsers} from "../../js/drawerapp/utils";

export class Gallery extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            offset: props.t.state.gamesAsDrawer.length,
            limit: 10,
            canGetMore: true,
            prevY: 0
        };
    }

    componentDidMount() {
        this.observer = new IntersectionObserver(
            this.handleObserver.bind(this),
            {
                root: null,
                rootMargin: '0px',
                threshold: 1.0
            }
        );
        this.observer.observe(this.loadingRef);
    }

    handleObserver(entities, observer) {
        const
            {y} = entities[0].boundingClientRect,
            {t} = this.props,
            {prevY, offset, limit, canGetMore} = this.state
        ;
        if ((prevY === 0 || prevY > y) && canGetMore) {
            t.socket.call('games.getByDrawerId', {id: t.state.vk_user.id, offset, limit}, async r => {
                await getVKUsers(r.response.map(value => value.drawerId));
                t.setState({gamesAsDrawer: [...t.state.gamesAsDrawer, ...r.response]});
                this.setState({canGetMore: r.response.length > 0});
                this.forceUpdate();
            });
            this.setState({offset: offset + 10});
        }
        this.setState({prevY: y});
    }

    shareStory(word, gameId, pic) {
        try {
            this.props.t.setPopout(<ScreenSpinner/>);
            const
                {createCanvas, loadImage} = require('canvas')
            ;

            loadImage(getSrcUrl(require(`../../assets/drawing/stories/Story1.png`))).then(async background => {
                try {
                    const
                        canvas = createCanvas(background.width, background.height),
                        ctx = canvas.getContext('2d'),
                        canvasData = await getBase64Image(pic),
                        title = word.substring(0, 1).toUpperCase() + word.substring(1)
                    ;
                    ctx.drawImage(background, 0, 0);

                    ctx.font = '64px TT Commons Demibold';
                    ctx.textAlign = 'center';
                    ctx.fillStyle = '#616161';
                    ctx.fillText(title, 539, 708 + 13 + 489);

                    ctx.drawImage(await loadImage(canvasData), 198, 618, 683, 683);

                    await bridge.send('VKWebAppShowStoryBox', {
                        background_type: 'image',
                        blob: canvas.toDataURL('image/png'),
                        attachment: {
                            url: 'https://vk.com/app' + getUrlParams().vk_app_id,
                            text: 'go_to',
                            type: 'url'
                        }
                    });
                    this.props.t.socket.call('games.share', {gameId});
                    this.props.t.setPopout(null);
                } catch (e) {
                    console.error(e);
                    this.props.t.setPopout(null);
                    this.props.t.setState({
                        snackbar: <Snackbar onClose={() => this.props.t.setState({snackbar: null})}>
                            Произошла ошибка
                        </Snackbar>
                    });
                }
            });
        } catch (e) {
            console.error(e);
            this.props.t.setPopout(null);
            this.props.t.setState({
                snackbar: <Snackbar onClose={() => this.props.t.setState({snackbar: null})}>
                    Произошла ошибка
                </Snackbar>
            });
        }
    }

    render() {
        const
            {t} = this.props,
            {banners, defaultBannerData} = t.state,
            isDesktop = isPlatformDesktop(),
            {gamesAsDrawer} = t.state
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                >
                    {
                        isDesktop && 'Твоя галерея'
                    }
                </PanelHeader>
                <div className='Gallery'>
                    {
                        !isDesktop && <h2 style={{textAlign: 'left'}}>Твоя галерея</h2>
                    }
                    <p style={{margin: !isDesktop && '8px 0 0 0'}}>
                        Выберите нужную картину, чтобы посмотреть информацию и выбрать действие:
                    </p>
                    <div style={{
                        marginTop: isDesktop ? 18 : 24,
                        gridGap: isDesktop ? 8 : 15,
                        paddingBottom: banners[0] || true && 140
                    }} className='Images_Grid'>
                        {
                            gamesAsDrawer.map((value, index) =>
                                <div key={`Image_${index}`} onClick={async () => {
                                    //this.shareStory(value['Word.nom'], value.id, value['Picture.url']);
                                    await t.setState({
                                        like_currentPictureSelected: index,
                                        like_method: 'games.getByDrawerId',
                                        like_param: 'gamesAsDrawer',
                                        like_user_id: t.state.vk_user.id
                                    });
                                    t.go('picture_info');
                                }}>
                                    <img alt='img' src={value['Picture.url']}/>
                                </div>
                            )
                        }
                    </div>
                    {
                        banners[0] &&
                        <PromoBanner
                            bannerData={banners[0]}
                            onClose={() => {
                                banners[0] = false;
                                t.setState({banners});
                            }}
                        />
                    }
                </div>
                <div ref={ref => this.loadingRef = ref}/>
            </React.Fragment>
        )
    }
}

Gallery.defaultProps = {};

Gallery.propTypes = {
    t: PropTypes.object
};

export default Gallery;