import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {
    Avatar,
    Button,
    IconButton,
    Input,
    PanelHeader,
    PanelHeaderBack,
    ScreenSpinner, SimpleCell,
    Snackbar,
    WriteBarIcon
} from "@vkontakte/vkui";
import {
    Icon16Cancel,
    Icon16Chevron, Icon16Recent,
    Icon24Like,
    Icon28ChevronRightOutline,
    Icon28ReportOutline,
    Icon28ShareOutline, Icon28WriteOutline
} from "@vkontakte/icons";
import {
    decOfNum,
    getBase64Image,
    getSrcUrl,
    getUrlParams,
    isPlatformDesktop, isPlatformIOS,
    openUrl,
    shortIntegers
} from "../../js/utils";
import {getVKUsers, name_cases, vk_local_users, vkApiRequest} from "../../js/drawerapp/utils";
import bridge from "@vkontakte/vk-bridge";

const isDesktop = isPlatformDesktop();

export class FriendLobby extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            removed: false,
            members: [],
            splashShow: false
        };

        setTimeout(async () => {
            const {friend_lobby} = this.props.t.state;
            await getVKUsers(friend_lobby.members);
            this.setState({members: friend_lobby.members});
        }, 1);
    }

    componentDidMount() {
        const {socket, state} = this.props.t;
        socket.unsubscribe('changeLobbyUsers', 'gameStart', 'setWord', 'gameEnd', 'draw', 'removeLobby', 'wakeUp');
        socket.subscribe('wakeUp', async r => {
            if (r.user_ids.indexOf(state.user.id) > -1) {
                for (let i = 0; i < 3; i++) {
                    setTimeout(async () => {
                        if(bridge.supports('VKWebAppTapticNotificationOccurred')){
                            await bridge.send('VKWebAppTapticNotificationOccurred', {type: 'success'});
                        }
                        await this.setState({splashShow: !this.state.splashShow});
                        setTimeout(() => {
                            this.setState({splashShow: !this.state.splashShow});
                        }, 100);
                    }, 200 + i * 200)
                }
            }
        });
        socket.subscribe('removeLobby', async r => {
            this.setState({removed: true});
        });
        socket.subscribe('changeLobbyUsers', async r => {
            const {friend_lobby} = this.props;
            await getVKUsers(r.players);
            friend_lobby.members = r.players;
            this.props.t.setState({friend_lobby});
            setTimeout(async () => {
                await this.setState({members: friend_lobby.members});
                this.forceUpdate();
                if (r.players.indexOf(state.user.id) === -1 && this.props.friend_lobby.owner_id !== state.user.id) {
                    this.props.t.back();
                    if (this.state.removed) {
                        this.props.t.setState({
                            snackbar: <Snackbar onClose={() => this.setState({snackbar: null})}>
                                Лобби было удалено
                            </Snackbar>
                        });
                    } else {
                        this.props.t.setState({
                            snackbar: <Snackbar onClose={() => this.setState({snackbar: null})}>
                                Вас выгнали из лобби
                            </Snackbar>
                        });
                    }
                }
            }, 400);
        });
        socket.subscribe('setWord', async r => {
            this.props.t.setState({word: r.word});
        });
        socket.subscribe('gameStart', async r => {
            const {lobbyId, drawerId} = r;
            await this.props.t.setState({lobbyId, drawerId, gameStart: Date.now(), isOnlineDrawing: true});
            this.props.t.go('game');
        });
    }

    render() {
        const
            {t, friend_lobby} = this.props,
            {socket} = t,
            {user} = t.state,
            {members, splashShow} = this.state
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                >
                    {
                        isDesktop && 'Дружеская игра'
                    }
                </PanelHeader>
                <div className='FriendSplash' style={{opacity: splashShow ? 1 : 0}}/>
                <div className='FriendLobby'>
                    {
                        !isDesktop && <h2 style={{textAlign: 'left'}}>Дружеская игра</h2>
                    }
                    <span className='Span'>
                        {
                            friend_lobby.owner_id === user.id ?
                                'Лобби создано. Участники:'
                                :
                                'Участники лобби:'
                        }
                    </span>
                    <div className='FriendsList'>
                        <div style={{marginTop: 8}}>
                            <SimpleCell
                                onClick={() => {
                                    if (friend_lobby.owner_id === user.id) {
                                        socket.call('friends.wakeUp', {user_ids: [user.id, ...members], owner_id: friend_lobby.owner_id});
                                    } else {
                                        socket.call('friends.wakeUp', {user_ids: [friend_lobby.owner_id], owner_id: friend_lobby.owner_id});
                                    }
                                }}
                                before={<Avatar size={32} shadow={false}
                                                src={vk_local_users[friend_lobby.owner_id].photo_100}/>}
                            >
                                {vk_local_users[friend_lobby.owner_id].first_name} {vk_local_users[friend_lobby.owner_id].last_name.substring(0, 1)}.
                            </SimpleCell>
                            {
                                members.map((value, index) =>
                                    <SimpleCell
                                        onClick={() => {
                                            socket.call('friends.wakeUp', {user_ids: [value], owner_id: friend_lobby.owner_id});
                                        }}
                                        key={`SCell_${index}`}
                                        before={<Avatar size={32} shadow={false}
                                                        src={vk_local_users[value].photo_100}/>}
                                        after={friend_lobby.owner_id === user.id &&
                                        <IconButton onClick={() => {
                                            socket.call('friends.kickFromLobby', {user_id: value});
                                        }}><Icon16Cancel fill='var(--icon_tertiary)'/></IconButton>}
                                    >
                                        {vk_local_users[value].first_name} {vk_local_users[value].last_name.substring(0, 1)}.
                                    </SimpleCell>
                                )
                            }
                        </div>
                    </div>
                    {
                        friend_lobby.owner_id === user.id && members.length > 0 &&
                        <Button
                            style={{marginTop: 24}}
                            size='m'
                            mode='gradient_violet'
                            onClick={() => {
                                socket.call('friends.startGame');
                            }}
                        >
                            Начать игру
                        </Button>
                    }
                </div>
            </React.Fragment>
        )
    }
}

FriendLobby.defaultProps = {};

FriendLobby.propTypes = {
    t: PropTypes.object,
    friend_lobby: PropTypes.object
};

export default FriendLobby;