import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, PanelHeader, PanelHeaderBack, PromoBanner, ScreenSpinner, Snackbar} from "@vkontakte/vkui";
import {getBase64Image, getSrcUrl, getUrlParams, isPlatformDesktop} from "../../js/utils";
import bridge from "@vkontakte/vk-bridge";
import {getVKUsers, vk_local_users} from "../../js/drawerapp/utils";

export class Author extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            offset: props.t.state.author_works.length,
            limit: 10,
            canGetMore: true,
            prevY: 0
        };
    }

    componentDidMount() {
        this.observer = new IntersectionObserver(
            this.handleObserver.bind(this),
            {
                root: null,
                rootMargin: '0px',
                threshold: 1.0
            }
        );
        this.observer.observe(this.loadingRef);
    }

    handleObserver(entities, observer) {
        const
            {y} = entities[0].boundingClientRect,
            {t} = this.props,
            {prevY, offset, limit, canGetMore} = this.state
        ;
        if ((prevY === 0 || prevY > y) && canGetMore) {
            t.socket.call('games.getByDrawerId', {id: t.state.author.id, offset, limit}, async r => {
                t.setState({author_works: [...t.state.author_works, ...r.response]});
                this.setState({canGetMore: r.response.length > 0});
                this.forceUpdate();
            });
            this.setState({offset: offset + 10});
        }
        this.setState({prevY: y});
    }

    render() {
        const
            {t} = this.props,
            {state, _tags} = t,
            isDesktop = isPlatformDesktop(),
            {author, author_works} = state
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                />
                <div className='Author'>
                    <div className='User_Header'>
                        <Avatar src={vk_local_users[author.id].photo_100} size={48} shadow={false}/>
                        <div className='UserTags'>
                            {
                                _tags().map(value =>
                                    author[value[0]] && React.cloneElement(value[1], {onClick: () => t.setActiveModal(value[0])})
                                )
                            }
                        </div>
                    </div>
                    <h3>{vk_local_users[author.id].first_name} {vk_local_users[author.id].last_name.substring(0, 1)}.</h3>
                    <h1>Работы игрока</h1>
                    <div style={{
                        marginTop: 24,
                        gridGap: isDesktop ? 8 : 15
                    }} className='Images_Grid'>
                        {
                            author_works.map((value, index) =>
                                <div key={`Image_${index}`} onClick={async () => {
                                    await t.setState({
                                        like_currentPictureSelected: index,
                                        like_method: 'games.getByDrawerId',
                                        like_param: 'author_works',
                                        like_user_id: author.id
                                    });
                                    const {history} = t.state;
                                    if (history[history.length - 1] === 'picture_info') {
                                        t.back();
                                    } else {
                                        t.go('picture_info');
                                    }
                                }}>
                                    <img alt='img' src={value['Picture.url']}/>
                                </div>
                            )
                        }
                    </div>
                </div>
                <div ref={ref => this.loadingRef = ref}/>
            </React.Fragment>
        )
    }
}

Author.defaultProps = {};

Author.propTypes = {
    t: PropTypes.object
};

export default Author;