import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, PanelHeader, PanelHeaderBack, PromoBanner, ScreenSpinner, Snackbar} from "@vkontakte/vkui";
import {decOfNum, getBase64Image, getSrcUrl, getUrlParams, isPlatformDesktop, shortIntegers} from "../../js/utils";
import bridge from "@vkontakte/vk-bridge";
import {Icon24Like} from "@vkontakte/icons";
import {getVKUsers} from "../../js/drawerapp/utils";

const isDesktop = isPlatformDesktop();

export class Likes extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            offset: props.t.state.randomGamesLike.length,
            limit: 10,
            canGetMore: true,
            prevY: 0
        };
    }

    async componentDidMount() {
        this.observer = new IntersectionObserver(
            this.handleObserver.bind(this),
            {
                root: null,
                rootMargin: '0px',
                threshold: 1.0
            }
        );
        this.observer.observe(this.loadingRef);
    }

    handleObserver(entities, observer) {
        const
            {y} = entities[0].boundingClientRect,
            {t} = this.props,
            {prevY, offset, limit, canGetMore} = this.state
        ;
        if ((prevY === 0 || prevY > y) && canGetMore) {
            t.socket.call('pictures.getRandomLikes', {offset, limit}, async r => {
                await getVKUsers(r.response.map(value => value.drawerId));
                t.setState({randomGamesLike: [...t.state.randomGamesLike, ...r.response]});
                this.setState({canGetMore: r.response.length > 0});
                this.forceUpdate();
            });
            this.setState({offset: offset + 10});
        }
        this.setState({prevY: y});
    }

    render() {
        const
            {t} = this.props,
            {banners} = t.state,
            isDesktop = isPlatformDesktop(),
            {randomGamesLike, topLikes} = t.state
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                />
                <div className='Likes'>
                    {
                        topLikes &&
                        <React.Fragment>
                            <p>
                                Лучшие картины:
                            </p>
                            <div className='Pictures_List'>
                                {
                                    topLikes.slice(0, isDesktop ? 4 : 3).map((value, index) =>
                                        <div key={`div__${index}`} onClick={async () => {
                                            await t.setState({
                                                like_currentPictureSelected: index,
                                                like_method: '',
                                                like_param: 'topLikes'
                                            });
                                            t.go('picture_info');
                                        }}>
                                            <img alt='img' src={value['Picture.url']}/>
                                            <div>
                                                {shortIntegers(value['Picture.likes'])}<Icon24Like width={10} height={10} style={{marginLeft: 4}}/>
                                            </div>
                                        </div>
                                    )
                                }
                            </div>
                        </React.Fragment>
                    }
                    {
                        randomGamesLike.length > 0 &&
                        <React.Fragment>
                            <p style={{margin: '24px 0 0 0'}}>
                                Выберите нужную картину, чтобы посмотреть информацию и оценить её:
                            </p>
                            <div style={{marginTop: 18, gridGap: isDesktop ? 8 : 15, paddingBottom: banners[2] && 140}} className='Images_Grid'>
                                {
                                    randomGamesLike.map((value, index) =>
                                        <div key={`Image_${index}`} onClick={async () => {
                                            await t.setState({
                                                like_currentPictureSelected: index,
                                                like_method: 'pictures.getRandomLikes',
                                                like_param: 'randomGamesLike',
                                                like_user_id: t.state.vk_user.id
                                            });
                                            t.go('picture_info');
                                        }}>
                                            <img alt='img' src={value['Picture.url']}/>
                                        </div>
                                    )
                                }
                            </div>
                            {
                                banners[2] &&
                                <PromoBanner
                                    bannerData={banners[2]}
                                    onClose={() => {
                                        banners[2] = false;
                                        t.setState({banners});
                                    }}
                                />
                            }
                        </React.Fragment>
                    }
                    <div ref={ref => this.loadingRef = ref}/>
                </div>
            </React.Fragment>
        )
    }
}

Likes.defaultProps = {};

Likes.propTypes = {
    t: PropTypes.object
};

export default Likes;