import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar} from "@vkontakte/vkui";
import {isPlatformDesktop} from "../../js/utils";
import {getVKUsers} from "../../js/drawerapp/utils";

let interval;

export class Search extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            lobbyPlayers: [],
            lobbyPlayers_: [],
            point_count: 0,
            starting: false
        };
    }

    componentWillUnmount() {
        clearInterval(interval);
    }

    componentDidMount() {
        interval = setInterval(() => {
            const {point_count} = this.state;
            this.setState({point_count: point_count >= 3 ? 0 : point_count + 1});
        }, 800);

        const {socket} = this.props.t;
        socket.unsubscribe('changeLobbyUsers', 'gameStart', 'setWord', 'gameEnd', 'draw', 'gameStarting');
        socket.subscribe('changeLobbyUsers', async r => {
            this.setState({lobbyPlayers: await getVKUsers(r.players), lobbyPlayers_: r.players});
        });
        socket.subscribe('setWord', async r => {
            this.props.t.setState({word: r.word});
        });
        socket.subscribe('gameStart', async r => {
            //console.log('Игра начинается');
            const {lobbyId, drawerId} = r;
            await this.props.t.setState({lobbyId, drawerId, gameStart: Date.now(), isOnlineDrawing: true});
            await new Promise(resolve => {
                const {lobbyPlayers, lobbyPlayers_} = this.state;
                console.log(`Получено игроков: ${lobbyPlayers.length} а должно: ${lobbyPlayers_.length}`);
                let interval;
                interval = setInterval(() => {
                    if (lobbyPlayers_.length === lobbyPlayers.length) {
                        clearInterval(interval);
                        console.log('Все игроки получены', lobbyPlayers);
                        resolve(true);
                    }
                }, 400);
            });
            this.props.t.go('game');
        });
        socket.subscribe('gameStarting', async r => {
            this.setState({starting: true});
        });
        socket.call('games.search');
    }

    render() {
        const
            {t} = this.props,
            lobbyPlayers = this.state.lobbyPlayers.filter(user => user.id !== t.state.vk_user.id),
            players =
                <svg width="274" height="316" viewBox="0 0 274 316" fill="none" xmlns="http://www.w3.org/2000/svg"
                     style={{marginTop: isPlatformDesktop() ? 72 : '8.86699507vh'}}
                     onClick={() => {
                         if (!this.state.starting) {
                             t.back();
                         }
                     }}
                >
                    <rect x="63" y="84" width="148" height="148" rx="74" fill="url(#paint0_linear)"/>
                    <circle className='Search_Avatar' cx="137" cy="158" r="48" fill="url(#pattern0)"/>
                    <line x1="27.866" y1="26.5" x2="60.366" y2="82.7916" stroke="var(--border_players_color)"
                          strokeWidth="2" strokeDasharray="4 4"/>
                    <rect x="1.5" y="1.5" width="51" height="51" rx="25.5"
                          fill="var(--background_content)"/>
                    <circle cx="27" cy="27" r="24" fill="url(#pattern1)"/>
                    <rect x="1.5" y="1.5" width="51" height="51" rx="25.5" stroke="var(--border_players_color)"
                          strokeWidth="3"/>
                    <line y1="-1" x2="65" y2="-1" transform="matrix(-0.5 0.866025 0.866025 0.5 246.88 27)"
                          stroke="var(--border_players_color)" strokeWidth="2" strokeDasharray="4 4"/>
                    <rect x="1.5" y="-1.5" width="51" height="51" rx="25.5" transform="matrix(-1 0 0 1 274 3)"
                          fill="var(--background_content)"/>
                    <rect x="1.5" y="-1.5" width="51" height="51" rx="25.5" transform="matrix(-1 0 0 1 274 3)"/>
                    <circle cx="247" cy="27" r="24" fill="url(#pattern2)"/>
                    <rect x="1.5" y="-1.5" width="51" height="51" rx="25.5" transform="matrix(-1 0 0 1 274 3)"
                          stroke="var(--border_players_color)" strokeWidth="3"/>
                    <line y1="-1" x2="65" y2="-1" transform="matrix(0.5 -0.866025 -0.866025 -0.5 27 289)"
                          stroke="var(--border_players_color)" strokeWidth="2" strokeDasharray="4 4"/>
                    <rect x="-1.5" y="1.5" width="51" height="51" rx="25.5" transform="matrix(1 0 0 -1 3 316)"
                          fill="var(--background_content)"/>
                    <rect x="-1.5" y="1.5" width="51" height="51" rx="25.5" transform="matrix(1 0 0 -1 3 316)"/>
                    <circle cx="27" cy="289" r="24" fill="url(#pattern3)"/>
                    <rect x="-1.5" y="1.5" width="51" height="51" rx="25.5" transform="matrix(1 0 0 -1 3 316)"
                          stroke="var(--border_players_color)" strokeWidth="3"/>
                    <line x1="246.014" y1="289.5" x2="213.514" y2="233.208" stroke="var(--border_players_color)"
                          strokeWidth="2" strokeDasharray="4 4"/>
                    <rect x="272.5" y="314.5" width="51" height="51" rx="25.5" transform="rotate(-180 272.5 314.5)"
                          fill="var(--background_content)"/>
                    <rect x="272.5" y="314.5" width="51" height="51" rx="25.5"
                          transform="rotate(-180 272.5 314.5)"/>
                    <circle cx="247" cy="289" r="24" fill="url(#pattern4)"/>
                    <rect x="272.5" y="314.5" width="51" height="51" rx="25.5" transform="rotate(-180 272.5 314.5)"
                          stroke="var(--border_players_color)" strokeWidth="3"/>
                    <defs>
                        <linearGradient id="paint0_linear" x1="63" y1="84" x2="211" y2="232"
                                        gradientUnits="userSpaceOnUse">
                            <stop stopColor="#F05F88"/>
                            <stop offset="1" stopColor="#FAAF1A"/>
                        </linearGradient>

                        <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use href="#image0" transform="scale(0.005)"/>
                        </pattern>
                        <image href={t.state.vk_user.photo_max_orig} id="image0" width="200" height="200"/>

                        <pattern id="pattern1" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use href="#image1" transform="scale(0.005)"/>
                        </pattern>
                        <image href={lobbyPlayers[0] && lobbyPlayers[0].photo_max_orig} id="image1" width="200"
                               height="200"/>

                        <pattern id="pattern2" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use href="#image2" transform="scale(0.005)"/>
                        </pattern>
                        <image href={lobbyPlayers[1] && lobbyPlayers[1].photo_max_orig} id="image2" width="200"
                               height="200"/>

                        <pattern id="pattern3" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use href="#image3" transform="scale(0.005)"/>
                        </pattern>
                        <image href={lobbyPlayers[2] && lobbyPlayers[2].photo_max_orig} id="image3" width="200"
                               height="200"/>

                        <pattern id="pattern4" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use href="#image4" transform="scale(0.005)"/>
                        </pattern>
                        <image href={lobbyPlayers[3] && lobbyPlayers[3].photo_max_orig} id="image4" width="200"
                               height="200"/>
                    </defs>
                </svg>,
            {point_count} = this.state
        ;

        return (
            <div className='Search'>
                <h1>{this.state.starting ? `Очищаем холст${new Array(point_count).fill('.').join('')}` : `Ищем игроков${new Array(point_count).fill('.').join('')}`}</h1>
                {players}
            </div>
        )
    }
}

Search.defaultProps = {};

Search.propTypes = {
    t: PropTypes.object
};

export default Search;